                            <em>{{userName}}</em>
                        </a>
                    </li>
                    <li class="sr-nav-item">
                        <a href="javascript:;" class="sr-navblink" title="退出" @click="signOut()">
                            <em>退出</em>
                        </a>
                    </li>
                </ul>
            </div>
            <div class="sr-side">
                <a class="side-btn open">
                    <i class="fs fs-side-retra"></i>
                </a>
                <div class="sr-side-scroll">
                    <div class="sr-search">
                        <input class="sear_input" type="text" placeholder="船名/IMO" value=""
