                            <div class="name">Cubic：</div>
                            <div class="text">{{shipMessage.Cubic}}</div>
                        </div>
                        <div class="column_two">
                            <div class="name">DWT：</div>
                            <div class="text">{{shipMessage.SDWT | NumFormat}}Mt</div>
                            <div class="name">LOA：</div>
                            <div class="text">{{shipMessage.LOA}}</div>
                        </div>
                        <div class="column_two">
                            <div class="name">Beam：</div>
                            <div class="text">{{shipMessage.Beam}}</div>
                            <div class="name">Hull Type：</div>
                            <div class="text">{{shipMessage.HullType}}</div>
                        </div>
                    </div>
                    <div class="column_container">
                        <div class="column_two">
                            <div class="name">最新时间：</div>
                            <div class="text">{{shipMessage.newTime}}</div>
                            <div class="name">航速：</div>
                            <div class="text">{{shipMessage.shipSpeed}}节</div>
                        </div>
                        <div class="column_two">
                            <div class="name">吃水：</div>
                            <div class="text">{{shipMessage.shipWaterline}}m</div>
                            <div class="name">最大吃水：</div>
                            <div class="text">{{shipMessage.shipFullWaterline}}m</div>
                        </div>
                        <div class="column_two">
                            <div class="name">目的地：</div>
                            <div class="text">{{shipMessage.destination}}</div>
                            <div class="name">ETA：</div>
                            <div class="text">{{shipMessage.ETA}}</div>
                        </div>
                        <div class="column_two">
                            <div class="name">始发港：</div>
                            <div class="text">{{shipMessage.shipInitial}}</div>
                            <div class="name">当前海区：</div>
                            <div class="text">{{shipMessage.nowSeaArea}}</div>
                        </div>
                        <div class="column_two lastli">
                            <div class="name sr-d1-label">时间范围：</div>
                            <div class="time_range">
                                <div class="time">
                                    <span class="d1-time-cpm d1-time-cpm1">
										<el-date-picker
                                                prefix-icon="fs-calendar"
                                                v-model="time1"
                                                @change="changeRouteTime"
                                                class="timeP1"
                                                size="mini"
                                                type="datetime"
                                                placeholder="选择日期"
                                                :picker-options="pickerOptions0">
					                     </el-date-picker>
									</span>
                                </div>
                                <em class="sr-lineb">-</em>
                                <div class="time">
                                    <span class="d1-time-cpm d1-time-cpm1">
										<el-date-picker
                                                prefix-icon="fs-calendar"
                                                v-model="time2"
                                                @change="changeRouteTime"
                                                class="timeP1"
                                                size="mini"
                                                type="datetime"
                                                placeholder="选择日期"
                                                :picker-options="pickerOptions1">
					                    </el-date-picker>
									</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="operate">
                        <div class="item tryQuery">轨迹查询</div>
                        <div class="item abolish" style="display: none; background:#D9B300;">清除轨迹</div>
