                        </div>
                        <div class="sr-row">
                            <span class="sr-label">提醒邮箱</span>
                            <input type="text" class="sr-inp" placeholder="huaxianzi_888@163.com">
                        </div>
                        <div class="sr-row">
                            <span class="sr-label">提醒间隔</span>
                            <select class="sr-sel">
                                <option></option>
                            </select>
                        </div>
                        <div class="sr-row">
                            <span class="sr-label"></span>
                            <button class="sr-btn">添加</button>
                        </div>
                    </div>
                </div>
                <!--弹出图层3-->
<!---<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<-->

