                };

                $.ajax({
                    type: "POST",
                    url: this.url+"/api/appjson/location.json",
                    data: JSON.stringify(obj),
                    dataType: "json",
                    contentType:"application/json",
                    success: function(data) {

                        that.reachLogs = data.datas.data;

                        that.total = data.total;

                    },
                    error:function(error) {

