                //获取地址拦？后面的vue值 $.getHref('name');
                function getHref(key) {
                    var str = window.location.href;
                    str = str.substring(str.indexOf('?') + 1);
                    var end = false;
                    var arrHref = str.split('&');
                    for(var i = 0; i < arrHref.length; i++) {
                        var chilrenArr = arrHref[i].split('=');
                        if(chilrenArr[0] == key) {
                            end = chilrenArr[1];
                            break;
                        }
                    }
                    return end;
