                //滚动条缩小地图
                $('.reduce').click(function (){

                    var c = parseInt(map.getZoom());
                    //最小层级是2
                    if(c-2>0){

                        --c;

                        var c1= (1-parseInt(c - 2 )/10);

                        var heights = (parseInt($('.scroll1-height1').css('height'))-18)*c1;

                        map.setZoom(c);

                        $('.dieblock').css({'top':''+heights+'px'});

                    }else{

