3{font-weight:400;}\r\nh4,h5,h6{font-size:100%;font-weight:400;}\r\nbutton,input,select,textarea{font-size:100%;}\r\ninput,button,textarea,select,optgroup,option{font-family:inherit;font-size:inherit;font-style:inherit;font-weight:inherit;outline:0;}\r\npre{white-space:pre-wrap;white-space:-moz-pre-wrap;white-space:pre-wrap;white-space:-o-pre-wrap;word-wrap:break-word;}\r\ntextarea{overflow:auto;resize:none;}\r\n\r\n/** 初始化全局标签 **/\r\nbody{line-height:24px;font:14px Helvetica Neue,Helvetica,PingFang SC,\\5FAE\\8F6F\\96C5\\9ED1,Tahoma,Arial,sansr-serif,\"Microsoft YaHei\",\"simsun\";}\r\nhr{height:1px;margin:10px 0;border:0;clear:both;}\r\na{color:#333;text-decoration:none;}\r\na:hover{color:#777;}\r\na cite{font-style:normal;*cursor:pointer;}\r\ni,em{font-style:normal;}\r\ninput::-webkit-input-placeholder,textarea::-webkit-input-placeholder{color:#aaa;font-size:14px;}\r\ninput:-moz-placeholder,textarea:-moz-placeholder{color:#aaa;font-size:14px;}\r\ninput::-moz-placeholder,textarea::-moz-placeholder{color:#aaa;font-size:14px;}\r\ninput:-msr-input-placeholder,textarea:-msr-input-placeholder{color:#aaa;font-size:14px;}\r\n::-webkit-scrollbar{width:auto;background-color:#e9edf4;}\r\n::-webkit-scrollbar-thumb{background-color:#ccc;border-radius:2em;-webkit-border-radius:2em;-moz-border-radius:2em;-o-border-radius:2em;}\r\n::-webkit-scrollbar-thumb:hover{background-color:#9bbbfa;}\r\n\r\n/*清除浮动*/\r\n.clearfix:after{content:\".\";display:block;height:0;visibility:hidden;clear:both;}\r\n.clearfix{zoom:1;}\r\n\r\n.sr-layout-body{overflow:hidden;}\r\n/*头部*/\r\n.sr-header{background-color:#181818;position:relative;z-index:10;height:50px;}\r\n.sr-header a:hover{transition:all .5s;-webkit-transition:all .5s;}\r\n.sr-logo{position:absolute;left:0;top:0;width:260px;height:100%;line-height:50px;color:#fff;font-size:18px;text-indent:135px;background:url(../images/s_logo.png) -25px center no-repeat;background-size:190px auto;}\r\n.sr-nav{position:relative;padding:0 10px;border-radius:2px;font-size:0;box-sizing:border-box;}\r\n.sr-nav *{font-size:14px;}\r\n.sr-nav .sr-nav-item,.sr-nav-left .sr-nav-item,.sr-search{position:relative;display:inline-block;*display:inline;*zoom:1;vertical-align:middle;}\r\n.sr-nav .sr-nav-item a.sr-navblink,.sr-nav-left .sr-nav-item a.sr-navblink{display:block;color:#fff;padding:0 20px;line-height:50px;transition:all .3s;-webkit-transition:all .3s;}\r\n.sr-layout-right{position:absolute !important;right:0;top:0;}\r\n\r\n/*左侧边栏*/\r\n.sr-side{border-top:1px solid #4f4f4f;position:fixed;bottom:0;z-index:999;background:#181818;top:50px;width:260px;}\r\n.sr-side-scroll{background:#181818;width:260px;height:100%;overflow-x:hidden;}\r\n/* side start */\r\n.side-btn{text-align:center;}\r\n.side-btn i.fs-side-retra{font-size:20px;font-weight:700;color:#E06514; height:136px;line-height:136px;display:block;transition:all 0.4s ease;-moz-transition:all 0.4s ease;-webkit-transition:all 0.4s ease;-o-transition:all 0.4s ease;}\r\n.side-btn i.fs-side-retra:before{color:#E06514;}\r\n.mar-top-10{margin-top:10px;}\r\n.sr-search{position:relative;}\r\n.sr-search input{margin:10px;width:228px;height:44px;line-height:44px;border:1px solid #4F4F4F;background:#181818;font-size:14px;color:#aaa;padding-left:10px;border-radius:2px;}\r\n.sr-search a{position:absolute;right:26px;top:24px;display:block;width:10px;height:10px;}\r\n.sr-search i{font-size:18px;opacity:0.5px;color:#fff;}\r\n.gk-search a{margin-left:10px;display:block;width:220px;height:44px;line-height:44px;padding-left:20px;background:#28282F;color:#fff;transition:all 2s ease;-moz-transition:all 2s ease;-webkit-transition:all 3s ease;-o-transition:all 2s ease;}\r\n.gk-search a i{color:#fff;display:inline-block;margin-top:-2px;font-size:20px;margin-right:20px;vertical-align:middle;}\r\n.ul_hx{display:none;margin-left:10px;width:240px;background:#28282F;}\r\n.sr-nav-tree{margin-left:10px;width:240px;}\r\n.sr-nav-tree li{position:relative;color:#fff;height:44px;line-height:44px;}\r\n.sr-nav-tree li .add{position:absolute;color:#aaa;font-size:28px;right:15px;}\r\n.sr-nav-tree li input{background:#28282F;width:153px;height:34px;line-height:36px;border:1px solid #5C5C5C;margin-left:10px;border-radius:2px;}\r\n.calc{width:240px;height:44px;text-align:center;}\r\n.calc a{margin:10px 53px;display:block;font-size:14px;width:155px;height:28px;background:#2e93fe;color:#fff;border-radius:2px;padding-top:8px;box-shadow:inset 0 1px 0 0 rgba(255,255,255,0.21);}\r\n/* side end */\r\n/* 模态框 */\r\n.side-btn{display:block;margin-right:0;width:30px;height:136px;line-height:136px;background:#28282F;position:absolute;right:-30px;top:50%;border-radius:0 4px 4px 0;}\r\n.icon_down{display:inline-block;margin-left:50px;transition:all 0.3s ease;-moz-transition:all 0.3s ease;-webkit-transition:all 0.5s ease;-o-transition:all 0.3s ease;}\r\n\r\n/*右侧主体内容*/\r\n.sr-body{position:absolute;left:260px;right:0;top:50px;bottom:0;z-index:1;width:auto;overflow:hidden;box-sizing:border-box;}\r\n\r\n/*放大缩小*/\r\n.sr-zooming{background:#212121;width:24px;height:500px;margin-top:-250px;position:absolute;top:50%;left:30px;z-index: 999;}\r\n.sr-zooming a{background:#000;display:inline-block;color:#fff;font-size:24px;font-family:SimSun;font-weight:700;width:24px;height:24px;text-align:center;line-height:24px;}\r\n.dieblock{background-color:#fff;border-radius:2px;display:inline-block;width:18px;height:18px;margin-left:-9px;position:absolute;left:50%;top:20%;cursor:pointer;}\r\n\r\n/*切换视图*/\r\n.sr-viewgraph{background:#28282f;width:200px;height:38px;position:absolute;right:18px;top:30px;z-index:9999;}\r\n.sr-viewgraph li{float:left;width:50%;}\r\n.sr-viewgraph li a{box-sizing:border-box;border:1px solid transparent;color:#aaa;display:inline-block;width:100%;line-height:36px;text-align:center;}\r\n.sr-viewgraph li a:hover{color:#fff;}\r\n.sr-viewgraph li.act a{border-color:#5c5c5c;color:#fff;}\r\n\r\n/*弹出层共用样式*/\r\n.sr-dialog{opacity:0.93;background:#212121;color:#fff;box-shadow:10px 20px 20px